 const makeid = (length) =>{
                              var result           = ["Process_"];
                              var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                              var charactersLength = characters.length;
                              for ( var i = 0; i < length; i++ ) {
                                result.push(characters.charAt(Math.floor(Math.random() *
                           charactersLength)));
                             }
                             return result.join('');
                          }

export const emptyBpmn = `<?xml version="1.0" encoding="UTF-8" standalone="no"?><bpmn2:definitions xmlns:bpmn2="http://www.omg.org/spec/BPMN/20100524/MODEL" xmlns:bpmndi="http://www.omg.org/spec/BPMN/20100524/DI" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" id="sample-diagram" targetNamespace="http://bpmn.io/schema/bpmn" xsi:schemaLocation="http://www.omg.org/spec/BPMN/20100524/MODEL BPMN20.xsd">
                            <bpmn2:process id="${makeid(7)}" isExecutable="true"/>
                            <bpmndi:BPMNDiagram id="BPMNDiagram_1">
                              <bpmndi:BPMNPlane bpmnElement="Process_1bzh145" id="BPMNPlane_1"/>
                            </bpmndi:BPMNDiagram>
                          </bpmn2:definitions>`;


